.game-container {
  background: #f2f2f2;
  border: 1px solid #cdcdcd;
  max-width: 1200px;
  height: 800px;
  margin: 0px auto;
  position: relative;
  overflow: hidden; }

.main-menu {
  background: white;
  height: 100%; }

.guesses {
  float: right;
  width: 30%; }
  .guesses h1 {
    color: blue;
    font-size: 30px; }

.screen-word {
  font-size: 24px;
  text-align: center;
  width: 60%;
  float: left;
  position: relative; }
  .screen-word:before {
    content: url("/images/theater.jpg");
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0; }



